import schemaToString from "../../parser/schemaToString";

const currentProjectReducer = (state, action) => {
  switch (action.type) {
    case "set_project":
      return action.project;
    case "parse_schema":
      return { ...state, parsedSchemaObjects: action.parsedSchema };
    case "update_schema_string":
      return { ...state, schemaString: action.schemaString };
    case "update_pos":
      return {
        ...state,
        parsedSchemaObjects: state.parsedSchemaObjects.map((table, index) =>
          index === action.tableIndex
            ? {
                ...table,
                data: {
                  ...table.data,
                  left: action.position.left,
                  top: action.position.top,
                },
              }
            : table
        ),
      };
    case "shift_tables":
      const shiftAmount = action.shiftAmount;
      const newTables = state.parsedSchemaObjects.map((table, index) => {

        if (index !== action.index) {
          if (action.direction === "right") {
            return {
              ...table,
              data: {
                ...table.data,
                left: table.data.left + shiftAmount,
              },
            };
          } else if (action.direction === "down") {
            return {
              ...table,
              data: {
                ...table.data,
                top: table.data.top + shiftAmount,
              },
            };
          }
        }
        return table;
      });
      return {
        ...state,
        parsedSchemaObjects: newTables,
      };

    case "update_table_color":
      return {
        ...state,
        parsedSchemaObjects: state.parsedSchemaObjects.map((table, index) =>
          index === action.tableIndex
            ? { ...table, data: { ...table.data, color: action.color } }
            : table
        ),
      };
    case "update_table_color":
      return {
        ...state,
        parsedSchemaObjects: state.parsedSchemaObjects.map((table, index) =>
          index === action.tableIndex
            ? { ...table, data: { ...table.data, color: action.color } }
            : table
        ),
      };
    case "update_field":
      return {
        ...state,
        parsedSchemaObjects: state.parsedSchemaObjects.map(
          (table, tableIndex) =>
            tableIndex === action.tableIndex
              ? {
                  ...table,
                  fields: table.fields.map((field, fieldIndex) =>
                    fieldIndex === action.fieldIndex
                      ? { ...field, [action.field]: action.value }
                      : field
                  ),
                }
              : table
        ),
      };
    case "delete_table":
      const schemaObjectsAfterDeletetion = state.parsedSchemaObjects.filter(
        (_, index) => {
          return index !== action.tableIndex;
        }
      );
      const schemaStringAfterDeletion = schemaToString(
        schemaObjectsAfterDeletetion
      );

      return {
        ...state,
        parsedSchemaObjects: schemaObjectsAfterDeletetion,
        schemaString: schemaStringAfterDeletion,
      };
    case "update_table":
      const updatedSchemaObjects = state.parsedSchemaObjects.map(
        (table, index) =>
          index === action.tableIndex ? { ...table, ...action.table } : table
      );
      const updatedSchemaString = schemaToString(updatedSchemaObjects);
      return {
        ...state,
        parsedSchemaObjects: updatedSchemaObjects,
        schemaString: updatedSchemaString,
      };
    default:
      return state;
  }
};

export default currentProjectReducer;
