import React, {
  useEffect,
  useReducer,
  createContext,
  useCallback,
  useState,
  useContext,
} from "react";
import debounce from "lodash.debounce";
import currentProjectReducer from "./reducer/currentProjectReducer";
import projectReducer from "./reducer/projectReducer";
import { parseSchema } from "../parser/db-parser";

const TableDataContext = createContext({});

export const TableDataContextProvider = ({ children }) => {
  const [projects, dispatchProjects] = useReducer(
    projectReducer,
    JSON.parse(localStorage.getItem("projects")) || []
  );

  const [currentProject, dispatchCurrentProject] = useReducer(
    currentProjectReducer,
    projects.length > 0
      ? projects[0]
      : { schemaString: "", parsedSchemaObjects: [] }
  );

  const [hoveredIndex, setHoveredIndex] = useState(null);
  const [zoom, setZoom] = useState(1);
  const [svgRef, setSvgRef] = useState(null);

  useEffect(() => {
    localStorage.setItem("projects", JSON.stringify(projects));
  }, [projects]);

  useEffect(() => {
    debouncedParseSchema(currentProject.schemaString,currentProject.parsedSchemaObjects);
  }, [currentProject.schemaString]);

  const switchProject = (projectId) => {
    const project = projects.find((project) => project.id === projectId);
    if (project) {
      dispatchCurrentProject({ type: "set_project", project });
    }
  };

  const createProject = (name) => {
    const newProject = {
      id: Date.now(),
      name,
      schemaString: "",
      parsedSchemaObjects: [],
    };
    dispatchProjects({ type: "create_project", payload: newProject });
  };

  const deleteProject = (projectId) => {
    dispatchProjects({ type: "delete_project", payload: projectId });
    const updatedProjects = projects.filter(
      (project) => project.id !== projectId
    );
    if (updatedProjects.length > 0) {
      switchProject(updatedProjects[0].id);
    } else {
      dispatchCurrentProject({
        type: "set_project",
        project: { schemaString: "", parsedSchemaObjects: [] },
      });
    }
  };

  const saveProject = () => {
    const updatedProjects = projects.map((project) =>
      project.id === currentProject.id ? { ...currentProject } : project
    );
    dispatchProjects({ type: "set_projects", payload: updatedProjects });
  };

  const debouncedParseSchema = useCallback(
    debounce((schemaString,parsedSchemaObjects=[]) => {
      const parsedSchema = parseSchema(
        schemaString,
        parsedSchemaObjects
      );
      dispatchCurrentProject({ type: "parse_schema", parsedSchema });
    }, 400),
    []
  );

  const deleteTable = (index)=>{
    dispatchCurrentProject({type:'delete_table',tableIndex:index});
  }

  const contextValue = {
    currentProject,
    projects,
    switchProject,
    createProject,
    deleteProject,
    saveProject,
    dispatchCurrentProject,
    zoom,
    setZoom,
    hoveredIndex,
    setHoveredIndex,
    svgRef,
    setSvgRef,
    deleteTable,
  };

  return (
    <TableDataContext.Provider value={contextValue}>
      {children}
    </TableDataContext.Provider>
  );
};

const useTableDataContext = () => useContext(TableDataContext);
export default useTableDataContext;
