import React, { useMemo } from "react";
import styles from "../../styles/styles";
import { defaultFontSize } from "../../styles/styles";
import useTableDataContext from "../../context/TableDataContext";

const TableHeader = ({
  xRectPos,
  yRectPos,
  xTextPos,
  yTextPos,
  name,
  height,
  color,
}) => {
  const { zoom } = useTableDataContext();

  const cellWidth = useMemo(() => styles.defaultCellWidth * zoom, [zoom]);
  const fontSize = useMemo(() => defaultFontSize * zoom, [zoom]);

  const headerElements = useMemo(
    () => (
      <>
        <rect
          x={xRectPos}
          y={yRectPos}
          width={cellWidth}
          height={height}
          fill={color || "green"}
        />
        <text
          x={xTextPos}
          y={yTextPos}
          fontFamily="monospace"
          fontSize={fontSize}
          fill="white"
          dominantBaseline="hanging"
        >
          {name}
        </text>
      </>
    ),
    [
      xRectPos,
      yRectPos,
      cellWidth,
      height,
      color,
      xTextPos,
      yTextPos,
      fontSize,
      name,
    ]
  );

  return headerElements;
};

export default TableHeader;
