export const exportSvgToImage = (
  svgElement,
  format = "png",
  scaleFactor = 4
) => {
  const clonedSvgElement = svgElement.cloneNode(true);

  const foreignObjects = clonedSvgElement.querySelectorAll("foreignObject");
  foreignObjects.forEach((fo) => fo.parentNode.removeChild(fo));

  const tables = svgElement.querySelectorAll("g");
  const bbox = Array.from(tables).reduce(
    (acc, table) => {
      const { x, y, width, height } = table.getBBox();
      acc.minX = Math.min(acc.minX, x);
      acc.minY = Math.min(acc.minY, y);
      acc.maxX = Math.max(acc.maxX, x + width);
      acc.maxY = Math.max(acc.maxY, y + height);
      return acc;
    },
    { minX: Infinity, minY: Infinity, maxX: -Infinity, maxY: -Infinity }
  );

  const padding = 40;
  const { minX, minY, maxX, maxY } = bbox;
  const svgWidth = maxX - minX + padding*2;
  const svgHeight = maxY - minY + padding*2;

  const canvas = document.createElement("canvas");
  const ctx = canvas.getContext("2d");

  canvas.width = svgWidth * scaleFactor;
  canvas.height = svgHeight * scaleFactor;
  ctx.scale(scaleFactor, scaleFactor);

  const img = new Image();
  img.onload = () => {
    ctx.drawImage(img, -minX+padding, -minY+padding);

    const dataURL = canvas.toDataURL(`image/${format}`);
    const link = document.createElement("a");
    link.download = `schema.${format}`;
    link.href = dataURL;
    link.click();
  };

  const svgData = new XMLSerializer().serializeToString(clonedSvgElement);
  img.src = "data:image/svg+xml;base64," + btoa(svgData);
};
