import React, { useRef, useState, useEffect, useCallback } from "react";
import useTableDataContext from "../context/TableDataContext";
import { AiOutlineZoomIn, AiOutlineZoomOut } from "react-icons/ai";
import { ButtonGroup, Button } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import SchemaDesigner from "./SchemaDesigner.jsx";
import debounce from "lodash.debounce";
import MiniMap from "./MiniMap.jsx";

const DesignView = ({ showToastMessage }) => {
  const svgRef = useRef(null);
  const containerRef = useRef(null);
  const shadowTimeoutRef = useRef(null);
  const { zoom, setZoom, setSvgRef, currentProject } = useTableDataContext();
  const { parsedSchemaObjects } = currentProject;
  const [dragging, setDragging] = useState(false);
  const [initialSvgPosition, setInitialSvgPosition] = useState({ x: 0, y: 0 });
  const [contextIndex, setContextIndex] = useState(null);
  const [containerScroll, setContainerScroll] = useState({ left: 0, right: 0 });
  const [showBottomShadow, setShowBottomShadow] = useState(false);

  const adjustSvgSize = useCallback(() => {
    const svg = svgRef.current;
    const bbox = svg.getBBox();
    if (svg) {
      const { minX, minY, maxX, maxY } =
        calculateBoundingBox(parsedSchemaObjects);
      const width = Math.max(bbox.width, maxX - minX, window.innerWidth / zoom);
      const height = Math.max(
        bbox.height,
        maxY - minY,
        window.innerHeight / zoom
      );
      svg.setAttribute("width", width);
      svg.setAttribute("height", height);
    }
  }, [zoom,parsedSchemaObjects]);

  const calculateBoundingBox = (tables) => {
    return tables.reduce(
      (box, table) => {
        const tableRight = table.data.left + table.data.width;
        const tableBottom = table.data.top + 200;

        return {
          minX: Math.min(box.minX, table.data.left),
          minY: Math.min(box.minY, table.data.top),
          maxX: Math.max(box.maxX, tableRight),
          maxY: Math.max(box.maxY, tableBottom),
        };
      },
      { minX: Infinity, minY: Infinity, maxX: -Infinity, maxY: -Infinity }
    );
  };

  const showBottomShadowWithTimeout = () => {
    setShowBottomShadow(true);
    clearTimeout(shadowTimeoutRef.current);
    shadowTimeoutRef.current = setTimeout(() => {
      setShowBottomShadow(false);
    }, 1000);
  };

  useEffect(() => {
    adjustSvgSize();
  }, [parsedSchemaObjects, zoom, adjustSvgSize]);

  useEffect(() => {
    if (svgRef !== null) {
      setSvgRef(svgRef);
    }
  }, [svgRef, setSvgRef]);

  const handleZoomIn = () => setZoom((prevZoom) => Math.min(prevZoom + 0.1, 2));
  const handleZoomOut = () =>
    setZoom((prevZoom) => Math.max(prevZoom - 0.1, 0.4));

  const handleMouseDown = (e) => {
    setDragging(true);
    setInitialSvgPosition({
      x: e.clientX + containerRef.current.scrollLeft,
      y: e.clientY + containerRef.current.scrollTop,
    });
  };

  const handleMouseMove = useCallback(
    debounce((e) => {
      if (!dragging) return;
      e.preventDefault();
      const container = containerRef.current;

      const left = initialSvgPosition.x - e.clientX;
      const top = initialSvgPosition.y - e.clientY;

      container.scrollLeft = left;
      container.scrollTop = top;
      setContainerScroll({ left, top });
    }, 1),
    [dragging, initialSvgPosition]
  );

  const handleMouseUp = () => {
    setDragging(false);
  };

  const handleWheel = useCallback(
    (e) => {
      e.preventDefault();
      const { minX, minY, maxX, maxY } =
        calculateBoundingBox(parsedSchemaObjects);
      const container = containerRef.current;
      const scrollAmount = 50 * zoom;

      let newScrollTop = container.scrollTop;
      let newScrollLeft = container.scrollLeft;

      if (e.deltaY < 0) {
        newScrollTop = Math.max(
          container.scrollTop - scrollAmount,
          minY * zoom - container.clientHeight * 0.25
        );
      } else if (e.deltaY > 0) {
        newScrollTop = Math.min(
          container.scrollTop + scrollAmount,
          maxY * zoom - container.clientHeight * 0.25
        );
      }

      if (e.deltaX < 0) {
        newScrollLeft = Math.max(
          container.scrollLeft - scrollAmount,
          minX * zoom - container.clientWidth * 0.25
        );
      } else if (e.deltaX > 0) {
        newScrollLeft = Math.min(
          container.scrollLeft + scrollAmount,
          maxX * zoom - container.clientWidth * 0.25
        );
      }

      if (newScrollTop + container.clientHeight >= maxY * zoom) {
        showBottomShadowWithTimeout();
      }

      container.scrollTop = newScrollTop;
      container.scrollLeft = newScrollLeft;

      setShowBottomShadow(newScrollTop < maxY * zoom - container.clientHeight);

      setContainerScroll({
        left: newScrollLeft,
        top: newScrollTop,
      });
    },
    [zoom, parsedSchemaObjects]
  );

  const handleMouseLeave = () => {
    setDragging(false);
  };

  return (
    <>
      <div
        ref={containerRef}
        style={{
          width: "100vw",
          height: "100vh",
          overflow: "hidden",
          cursor: dragging ? "grabbing" : "grab",
          willChange: dragging ? "scroll-position" : "auto",
          position: "relative",
        }}
        onMouseDown={handleMouseDown}
        onMouseMove={handleMouseMove}
        onMouseUp={handleMouseUp}
        onWheel={handleWheel}
        onMouseLeave={handleMouseLeave}
        onClick={() => setContextIndex(null)}
      >
        <svg
          ref={svgRef}
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          style={{ background: "white" }}
          id="main-svg"
        >
          <defs>
            <pattern
              id="grid-pattern"
              patternUnits="userSpaceOnUse"
              width={12 * zoom}
              height={12 * zoom}
            >
              <rect
                width={12 * zoom}
                height={12 * zoom}
                fill="none"
                stroke="#bbb"
                strokeWidth="0.5"
              />
            </pattern>
          </defs>
          <rect
            x="0"
            y="0"
            width="100%"
            height="100%"
            fill="url(#grid-pattern)"
          />
          {svgRef.current !== null && containerRef.current !== null && (
            <SchemaDesigner
              contextIndex={contextIndex}
              setContextIndex={setContextIndex}
              showToastMessage={showToastMessage}
              svgRef={svgRef}
              containerRef={containerRef}
            />
          )}
        </svg>
        {showBottomShadow && <div className="bottom-shadow" />}
      </div>
      <MiniMap
        svgRef={svgRef}
        containerRef={containerRef}
        zoom={zoom}
        containerScroll={containerScroll}
      />
      <div
        style={{
          position: "fixed",
          bottom: "20px",
          right: "20px",
          display: "flex",
          flexDirection: "column",
        }}
      >
        <ButtonGroup vertical>
          <Button variant="primary" onClick={handleZoomIn}>
            <AiOutlineZoomIn />
          </Button>
          <Button variant="primary" onClick={handleZoomOut}>
            <AiOutlineZoomOut />
          </Button>
        </ButtonGroup>
      </div>
    </>
  );
};

export default DesignView;
