import React, { useEffect, useState, useRef } from "react";
import EditorComponent from "./EditorComponent.jsx";
import DesignView from "./DesignView.jsx";
import useTableDataContext from "../context/TableDataContext.jsx";
import { useParams } from "react-router-dom";
import withToast from "./hoc/Toast.jsx";

const MainPage = ({showToastMessage}) => {
  const { switchProject, currentProject, projects } = useTableDataContext();
  const { id } = useParams();
  const projectId = parseInt(id);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (projectId !== currentProject.id) {
      setLoading(true);
      setTimeout(() => {
        switchProject(projectId);
        setLoading(false);
      }, 500);
    }
  }, [projectId, currentProject.id, switchProject]);

  return (
    <>
    {projects?.length>0 ? (<div
      className={`main-container ${loading ? "fade-out" : "fade-in"}`}
      key={id}
    >
      {!loading && (
        <>
          <div className="editor-component">
            <EditorComponent />
          </div>
          <DesignView showToastMessage={showToastMessage}/>
        </>
      )}
    </div>):(<p>Please Create New Project to Continue</p>)}
    </>
  );
};

export default withToast(MainPage);
