import styles from '../styles/styles.js';

import FieldTypes from '../constants/FieldTypes.js'

export const parseSchema = (schemaString, parsedSchema = []) => {
  let currentTable = null;
  let tablesToCreate = [];
  const lines = schemaString.split("\n");

  const parseLine = (line) => {
    if (!line.trim()) return;

    if (parseTableName(line)) return;
    if (parseTableEnding(line)) return;
    if (parseField(line)) return;
    console.error(`Unexpected token: ${line.split(" ")[0]}`);
  };

  const parseTableName = (line) => {
    const matches = line.match(/^(\S+)\s*\{/);
    const tableName = matches ? matches[1] : "";

    if (tableName) {
      const index = checkTableForExistence(tableName);
      if (index !== -1) {
        currentTable = { ...parsedSchema[index] };
        currentTable.fields = [];
        return true;
      }
      currentTable = { name: tableName, fields: [], data: {} };
      return true;
    }
    return false;
  };

  const checkTableForExistence = (tableName) => {
    const existingIdx = parsedSchema.findIndex(
      (table) => table.name === tableName
    );
    return existingIdx;
  };

  const checkFieldNameForExistence = (schema, fieldName) => {
    const fieldIndex = schema.fields.findIndex((field) => {
      return field.name === fieldName;
    });
    return fieldIndex;
  };

  const parseField = (line) => {
    if (!currentTable) return false;

    const tokens = line.split(" ");
    const name = tokens.shift(); // get the field name

    const prevSchema = parsedSchema.find((schema) => {
      return schema.name === currentTable.name;
    });
    let index;
    if (prevSchema) {
      index = checkFieldNameForExistence(prevSchema, name);
    } else {
      index = -1;
    }
    let field;
    if (index !== -1) {
      const fieldColor = prevSchema.fields[index].color;
      field = {
        name,
        type: "",
        allow_null: false,
        unique: false,
        fk: false,
        fk_table: "",
        fk_field: "",
        auto_increment: false,
        pk: false,
        color: fieldColor,
      };
    } else {
      field = {
        name,
        type: "",
        allow_null: false,
        unique: false,
        fk: false,
        fk_table: "",
        fk_field: "",
        auto_increment: false,
        pk: false,
        color: "white", // Set default color for new fields
      };
    }

    for (const token of tokens) {
      parseFieldToken(token, field);
    }
    currentTable.fields.push(field);
    return true;
  };

  const parseFieldToken = (token, field) => {
    if (token === "pk") {
      field.pk = true;
      return;
    }

    if (token === "increments") {
      field.auto_increment = true;
      return;
    }

    if (token === "null") {
      field.allow_null = true;
      return;
    }

    if (token === "unique") {
      field.unique = true;
      return;
    }

    if (FieldTypes.includes(token)) {
      field.type = token;
      return;
    }

    // check for relation
    if (token === ">") {
      field.fk = true;
      field.rel_type = "one_to_one";
      return;
    } else if (token === ">*") {
      field.fk = true;
      field.rel_type = "one_to_many";
      return;
    } else if (token === "*>*") {
      field.fk = true;
      field.rel_type = "many_to_many";
      return;
    } else if (token === "*>") {
      field.fk = true;
      field.rel_type = "many_to_one";
      return;
    }

    if (field.fk) {
      let data = token.split(".");
      let fk_field = data[data.length - 1];
      let fk_table = data.slice(0, data.length - 1).join("");
      if (fk_table && fk_field) {
        field.fk_table = fk_table;
        field.fk_field = fk_field;
        return;
      }
    }

    console.error(`Unexpected token: ${token}`);
  };

  const parseTableEnding = (line) => {
    if (!currentTable) return false;

    if (line === "}") {
      determineTablePosition();
      setTableColor();
      tablesToCreate.push(currentTable);
      currentTable = null;
      return true;
    }
    return false;
  };

  const setTableColor = () => {
    if (!currentTable.data.color) {
      currentTable.data.color = "green";
    }
  };

  const determineTablePosition = () => {
    const previousTable = tablesToCreate[tablesToCreate.length - 1];

    if (!currentTable.data.top || !currentTable.data.left) {
      if (previousTable) {
        let top = previousTable.data.top + styles.margin;
        let left =
          previousTable.data.left + previousTable.data.width + styles.margin;

        if (isOutOfScreen(top, left)) {
          top = previousTable.data.top + 60 + styles.margin;
          left = styles.margin;
        }

        currentTable.data.top = top;
        currentTable.data.left = left;
      } else {
        const initialTop = styles.margin;
        const initialLeft = styles.margin;
        currentTable.data.top = initialTop;
        currentTable.data.left = initialLeft;
      }
    }

    currentTable.data.width = styles.defaultCellWidth;
  };


  const isOutOfScreen = (top, left) => {
    const containerWidth = window.innerWidth;

    return left + 300 + styles.defaultCellWidth > containerWidth;
  };

  for (let line of lines) {
    parseLine(line.trim());
  }

  return tablesToCreate;
};
