import React,{useRef,useEffect} from 'react';

const MiniMap = ({ svgRef, containerRef, zoom, containerScroll }) => {
  const miniMapRef = useRef(null);
  const viewBoxRef = useRef(null);
  const miniMapContainerRef = useRef(null);

  useEffect(() => {
    const container = containerRef.current;
    const mainSvg = svgRef.current;
    const miniMap = miniMapRef.current;
    const viewBox = viewBoxRef.current;

    if (miniMap && mainSvg) {
      const miniMapViewBoxValue = `0 0 ${mainSvg.clientWidth} ${mainSvg.clientHeight}`;
      miniMap.setAttribute("viewBox", miniMapViewBoxValue);
    }

    if (viewBox && container) {
      const x = container.scrollLeft;
      const y = container.scrollTop;
      const width = container.clientWidth;
      const height = container.clientHeight;

      viewBox.setAttribute("x", x);
      viewBox.setAttribute("y", y);
      viewBox.setAttribute("width", width);
      viewBox.setAttribute("height", height);
    }
  }, [containerScroll, svgRef, containerRef]);

  return (
    <div
      ref={miniMapContainerRef}
      style={{
        position: "fixed",
        bottom: "20px",
        left: "320px",
        border: "1px solid #444",
        backgroundColor:"lightgrey"
      }}
    >
      <svg ref={miniMapRef} width="300" height="150">
        <use href={svgRef.current && "#main-svg"} />
        <rect ref={viewBoxRef} fill="none" stroke="blue" strokeWidth={10} />
      </svg>
    </div>
  );
};

export default MiniMap;