const schemaToString = (parsedSchema) => {
  let schemaString = "";

  parsedSchema.forEach((schema) => {
    schemaString += `${schema.name} {\n`;
    schema.fields.forEach((field, index) => {
      if (field.name) {
        schemaString += `\t${field.name} ${field.type}`;

        if (field.pk) schemaString += " pk";
        if (field.auto_increment) schemaString += " increments";
        if (field.allow_null) schemaString += " null";
        if (field.unique) schemaString += " unique";
        if (field.fk) {
          let relation = "";
          switch (field.rel_type) {
            case "one_to_one":
              relation = ">";
              break;
            case "one_to_many":
              relation = ">*";
              break;
            case "many_to_many":
              relation = "*>*";
              break;
            case "many_to_one":
              relation = "*>";
              break;
            default:
              relation = "";
          }
          schemaString += ` ${relation} ${field.fk_table}.${field.fk_field}`;
        }
        schemaString += "\n";
      } else {
        console.error(
          `Field Name not found at index ${index} in table ${schema.name}`
        );
      }
    });
    schemaString += `}\n`;
  });

  return schemaString;
};

export default schemaToString;
