import React from "react";
import { Editor } from "@monaco-editor/react";
import useTableDataContext from "../context/TableDataContext";

const EditorComponent = () => {
  
  const { currentProject, dispatchCurrentProject } = useTableDataContext();

  const handleEditorChange = (value) => {
    dispatchCurrentProject({
      type:"update_schema_string",
      schemaString: value
    })
  };

  return (
    <div key={currentProject.id} style={{ width: "100%", height: "100%" }}>
      <Editor
        height="100%"
        language="plaintext"
        theme="vs-dark"
        value={currentProject.schemaString}
        onChange={handleEditorChange}
      />
    </div>
  );
};

export default EditorComponent;
