import React, { useMemo } from "react";
import { defaultFontSize } from "../../styles/styles";
import useTableDataContext from "../../context/TableDataContext";

const TableField = ({
  xRectPos,
  fieldName,
  fieldType,
  fieldTextX,
  fieldTypeX,
  fieldY,
  highlighted,
  cumulativeHeight,
  height,
  width,
  color,
  pk
}) => {
  const { zoom } = useTableDataContext();
  const fontSize = useMemo(() => defaultFontSize * zoom, [zoom]);

  const fieldElements = useMemo(
    () => (
      <g key={`field${fieldName}`}>
        <rect
          x={xRectPos}
          y={cumulativeHeight}
          width={width}
          height={height}
          fill={highlighted ? "lightgray" : color || "lightgray"}
        />
        <text
          x={fieldTextX}
          y={fieldY}
          fontFamily="monospace"
          fontSize={fontSize}
          fill="black"
          fontWeight={pk?'bold':'normal'}
          dominantBaseline="hanging"
        >
          {fieldName}
        </text>
        <text
          x={fieldTypeX}
          y={fieldY}
          fontFamily="monospace"
          fontSize={fontSize}
          fill="black"
          textAnchor="end"
          fontWeight={pk?'bold':'normal'}
          dominantBaseline="hanging"
        >
          {fieldType}
        </text>
      </g>
    ),
    [
      xRectPos,
      cumulativeHeight,
      width,
      height,
      highlighted,
      color,
      fieldName,
      fieldTextX,
      fieldY,
      fieldTypeX,
      fieldType,
      fontSize,
    ]
  );

  return fieldElements;
};

export default TableField;
