import React from "react";
import { useNavigate } from "react-router-dom";
import { ListGroup, Container } from "react-bootstrap";
import useTableDataContext from "../context/TableDataContext";
import "bootstrap/dist/css/bootstrap.min.css";

const ProjectsList = () => {
  const { projects } = useTableDataContext();
  const navigate = useNavigate();

  const handleProjectClick = (projectId) => {
    navigate(`/schema/${projectId}`);
  };

  return (
    <Container>
      <h1 className="my-4">Projects</h1>
      {projects?.length>0 ? (<ListGroup>
        {projects.map((project) => (
          <ListGroup.Item
            key={project.id}
            action
            onClick={() => handleProjectClick(project.id)}
          >
            {project.name}
          </ListGroup.Item>
        ))}
      </ListGroup>): (<p>No Projects Found. Please Create New Project to Continue</p>)}
    </Container>
  );
};

export default ProjectsList;
