import React, { useState } from "react";
import {
  Navbar,
  Nav,
  Button,
  Container,
  Modal,
  Form,
  Dropdown,
} from "react-bootstrap";
import {
  AiOutlineClose,
  AiOutlineCloudDownload,
  AiOutlinePlus,
  AiOutlineSave,
} from "react-icons/ai";
import useTableDataContext from "../context/TableDataContext";
import { useNavigate } from "react-router-dom";
import { exportSvgToImage } from "../utils/exportUtils";
import withToast from "./hoc/Toast";

const NavbarComponent = ({ showToastMessage }) => {

  const {
    projects,
    createProject,
    saveProject,
    currentProject,
    deleteProject,
    svgRef,
  } = useTableDataContext();

  const [showNewProjectModal, setShowNewProjectModal] = useState(false);
  const [projectName, setProjectName] = useState("");
  const navigate = useNavigate();

  const handleExport = (format) => {
    if (!svgRef.current) return;
    exportSvgToImage(svgRef.current, format);
  };

  const handleNewProject = () => {
    setShowNewProjectModal(true);
  };

  const handleCreateProject = () => {
    if (projectName.trim()) {
      createProject(projectName);
      setProjectName("");
      setShowNewProjectModal(false);
    }
  };

  const handleDeleteProject = (projectId) => {
    const confirmed = window.confirm(
      "Are you sure you want to delete this project?"
    );
    if (confirmed) {
      deleteProject(projectId);
    }
  };

  const handleSwitchProject = (projectId) => {
    saveProject();
    navigate(`/schema/${projectId}`);
  };

  const handleSave = () => {
    saveProject();
    showToastMessage("Project saved successfully.");
  };
  
  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      handleCreateProject();
    }
  };

  return (
    <>
      <Navbar className="navbar-custom" expand="lg">
        <Container>
          <Navbar.Brand href="/">Schema Designer</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ml-auto">
              <Button
                variant="light"
                className="mr-2"
                onClick={handleNewProject}
              >
                <AiOutlinePlus className="mr-1" />
                New Project
              </Button>
              <Dropdown>
                <Dropdown.Toggle
                  variant="light"
                  id="dropdown-basic"
                  className="mr-2"
                >
                  <AiOutlineCloudDownload className="mr-1" />
                  Export
                </Dropdown.Toggle>
                <Dropdown.Menu>
                  <Dropdown.Item onClick={() => handleExport("png")}>
                    Export as PNG
                  </Dropdown.Item>
                  <Dropdown.Item onClick={() => handleExport("jpeg")}>
                    Export as JPEG
                  </Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>
              <Button variant="light" onClick={handleSave}>
                <AiOutlineSave className="mr-1" />
                Save
              </Button>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Nav
        variant="tabs"
        activeKey={currentProject?.id}
        className="justify-content-center mt-2"
      >
        {projects.map((project) => (
          <Nav.Item key={project.id}>
            <Nav.Link
              eventKey={project.id}
              onClick={() => handleSwitchProject(project.id)}
              className="d-flex align-items-center"
            >
              {project.name}
              <AiOutlineClose
                onClick={(e) => {
                  e.stopPropagation();
                  handleDeleteProject(project.id);
                }}
                className="ml-2"
              />
            </Nav.Link>
          </Nav.Item>
        ))}
      </Nav>

      <Modal
        show={showNewProjectModal}
        onHide={() => setShowNewProjectModal(false)}
      >
        <Modal.Header closeButton>
          <Modal.Title>Create New Project</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group controlId="formProjectName">
              <Form.Label>Project Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter project name"
                value={projectName}
                onChange={(e) => setProjectName(e.target.value)}
                onKeyPress={handleKeyPress}
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button
            variant="secondary"
            onClick={() => setShowNewProjectModal(false)}
          >
            Close
          </Button>
          <Button variant="light" onClick={handleCreateProject}>
            Create Project
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default withToast(NavbarComponent);
