const projectReducer = (state, action) => {
  switch (action.type) {
    case "create_project":
      return [...state, action.payload];
    case "delete_project":
      return state.filter((project) => project.id !== action.payload);
    case "set_projects":
      return action.payload;
    default:
      return state;
  }
};

export default projectReducer;
