import React, { useState, useEffect } from "react";
import { Modal, Button, Form, Row, Col } from "react-bootstrap";
import useTableDataContext from "../../context/TableDataContext";

const TableModal = ({ schema, isModalOpen, setIsModalOpen, tableIndex }) => {
  const { dispatchCurrentProject, saveProject } = useTableDataContext();
  const [modalData, setModalData] = useState(schema);

  useEffect(() => {
    setModalData(schema);
  }, [schema]);

  const handleFieldChange = (fieldIndex, field, value) => {
    const updatedFields = modalData.fields.map((f, index) =>
      index === fieldIndex ? { ...f, [field]: value } : f
    );
    setModalData({ ...modalData, fields: updatedFields });
  };

  const handleModalChange = (field, value) => {
    setModalData({ ...modalData, [field]: value });
  };

  const handleSave = () => {
    dispatchCurrentProject({
      type: "update_table",
      tableIndex,
      table: modalData,
    });
    saveProject();
    setIsModalOpen(false);
  };

  return (
    <Modal
      size="xl"
      aria-labelledby="contained-modal-title-vcenter"
      centered
      show={isModalOpen}
      onHide={() => setIsModalOpen(false)}
    >
      <Modal.Header closeButton>
        <Modal.Title>{`Edit table ${schema.name}`}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm={2}>
              Table Name
            </Form.Label>
            <Col sm={10}>
              <Form.Control
                type="text"
                value={modalData.name}
                onChange={(e) => handleModalChange("name", e.target.value)}
              />
            </Col>
          </Form.Group>
          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm={2}>
              Table Color
            </Form.Label>
            <Col sm={10}>
              <Form.Control
                type="color"
                value={modalData.data.color || "#ffffff"}
                onChange={(e) =>
                  handleModalChange("data", {
                    ...modalData.data,
                    color: e.target.value,
                  })
                }
              />
            </Col>
          </Form.Group>

          <table
            style={{
              width: "100%",
              borderCollapse: "collapse",
              marginTop: "16px",
            }}
          >
            <thead>
              <tr>
                <th>Field Name</th>
                <th>Type</th>
                <th>Color</th>
                <th>PK</th>
                <th>Allow Null</th>
                <th>Unique</th>
                <th>Auto Increment</th>
              </tr>
            </thead>
            <tbody>
              {modalData.fields.map((field, fieldIndex) => (
                <tr key={fieldIndex}>
                  <td>
                    <Form.Control
                      placeholder="Field name"
                      value={field.name}
                      onChange={(e) =>
                        handleFieldChange(fieldIndex, "name", e.target.value)
                      }
                    />
                  </td>
                  <td>
                    <Form.Control
                      placeholder="Type"
                      value={field.type}
                      onChange={(e) =>
                        handleFieldChange(fieldIndex, "type", e.target.value)
                      }
                    />
                  </td>
                  <td>
                    <Form.Control
                      type="color"
                      value={field.color || "#ffffff"}
                      onChange={(e) =>
                        handleFieldChange(fieldIndex, "color", e.target.value)
                      }
                    />
                  </td>
                  <td>
                    <Form.Check
                      type="switch"
                      id={`pk-switch-${fieldIndex}`}
                      label=""
                      checked={field.pk}
                      onChange={(e) =>
                        handleFieldChange(fieldIndex, "pk", e.target.checked)
                      }
                    />
                  </td>
                  <td>
                    <Form.Check
                      type="switch"
                      id={`null-switch-${fieldIndex}`}
                      label=""
                      checked={field.allow_null}
                      onChange={(e) =>
                        handleFieldChange(
                          fieldIndex,
                          "allow_null",
                          e.target.checked
                        )
                      }
                    />
                  </td>
                  <td>
                    <Form.Check
                      type="switch"
                      id={`unique-switch-${fieldIndex}`}
                      label=""
                      checked={field.unique}
                      onChange={(e) =>
                        handleFieldChange(
                          fieldIndex,
                          "unique",
                          e.target.checked
                        )
                      }
                    />
                  </td>
                  <td>
                    <Form.Check
                      type="switch"
                      id={`auto-increment-switch-${fieldIndex}`}
                      label=""
                      checked={field.auto_increment}
                      onChange={(e) =>
                        handleFieldChange(
                          fieldIndex,
                          "auto_increment",
                          e.target.checked
                        )
                      }
                    />
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={() => setIsModalOpen(false)}>
          Cancel
        </Button>
        <Button variant="primary" onClick={handleSave}>
          Save
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default TableModal;
