import React, { useCallback, useMemo, useState, useEffect } from "react";
import useTableDataContext from "../../context/TableDataContext";
import styles, {
  fieldCellPaddingY,
  headerCellPaddingY,
} from "../../styles/styles";
import TableHeader from "./TableHeader";
import TableField from "./TableField";
import TableModal from "./TableModal";
import { Button, ButtonGroup } from "react-bootstrap";
import { AiOutlineEdit, AiOutlineDelete } from "react-icons/ai";
import debounce from "lodash.debounce";
import { SCROLL_MARGIN, SCROLL_SPEED } from "../../constants/scrollConstant";

const Table = ({
  schema,
  index,
  svgRef,
  showToastMessage,
  contextIndex,
  setContextIndex,
  containerRef,
}) => {

  const {
    currentProject,
    dispatchCurrentProject,
    zoom,
    hoveredIndex,
    setHoveredIndex,
    deleteTable,
  } = useTableDataContext();
  const { parsedSchemaObjects } = currentProject;
  const [dragging, setDragging] = useState(false);
  const [initialMousePosition, setInitialMousePosition] = useState(null);
  const [initialTablePosition, setInitialTablePosition] = useState(null);
  const arrowMarkerSize = useMemo(() => 8 * zoom, [zoom]);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleEdit = () => {
    setIsModalOpen(true);
  };

  const handleDelete = () => {
    deleteTable(index);
    showToastMessage(`deleted ${schema.name}`);
  };

  const handleColorChange = (value) => {
    dispatchCurrentProject({
      type: "update_table_color",
      color: value,
      tableIndex: index,
    });
  };

  const autoScrollAndResizeContainer = (mouseX, mouseY) => {
    const container = containerRef.current;
    const svg = svgRef.current;

    if (container && svg) {
      const { left, top, right, bottom } = container.getBoundingClientRect();
  
      if (mouseX - left < SCROLL_MARGIN) {
        if (container.scrollLeft > 0) {
          container.scrollLeft -= SCROLL_SPEED;
        } else {
          shiftTables("right");
        }
      } else if (right - mouseX < SCROLL_MARGIN) {
        container.scrollLeft += SCROLL_SPEED;
      }
      if (mouseY - top < SCROLL_MARGIN) {
        if (container.scrollTop > 0) {
          container.scrollTop -= SCROLL_SPEED;
        } else {
          shiftTables("down");
        }
      } else if (bottom - mouseY < SCROLL_MARGIN) {
        container.scrollTop += SCROLL_SPEED;
      }
    }
  };

  const shiftTables = (direction) => {
    dispatchCurrentProject({
      type: "shift_tables",
      direction,
      shiftAmount: 10,
      index,
    });
  };

  const handleMouseMove = (e) => {
    if (dragging) {
      const { x, y } = getSvgPoint(e.clientX, e.clientY);
      const deltaX = (x - initialMousePosition.x) / zoom;
      const deltaY = (y - initialMousePosition.y) / zoom;
      let newLeft = initialTablePosition.left + deltaX;
      let newTop = initialTablePosition.top + deltaY;

      if (newLeft < 0) newLeft = 0;
      if (newTop < 0) newTop = 0;
      dispatchCurrentProject({
        type: "update_pos",
        tableIndex: index,
        position: { left: newLeft, top: newTop },
      });
    }
    autoScrollAndResizeContainer(e.clientX, e.clientY);
  };

  const handleMouseMoveDebounced = useCallback(debounce(handleMouseMove, 1), [
    initialMousePosition,
  ]);

  useEffect(() => {
    const handleMouseUp = () => {
      setDragging(false);
    };

    if (dragging) {
      window.addEventListener("mousemove", handleMouseMoveDebounced);
      window.addEventListener("mouseup", handleMouseUp);
    } else {
      window.removeEventListener("mousemove", handleMouseMoveDebounced);
      window.removeEventListener("mouseup", handleMouseUp);
    }
    return () => {
      window.removeEventListener("mousemove", handleMouseMoveDebounced);
      window.removeEventListener("mouseup", handleMouseUp);
    };
  }, [dragging, handleMouseMoveDebounced]);

  const getSvgPoint = (x, y) => {
    const svg = svgRef.current;
    const pt = svg.createSVGPoint();
    pt.x = x;
    pt.y = y;
    const svgPoint = pt.matrixTransform(svg.getScreenCTM().inverse());
    return { x: svgPoint.x, y: svgPoint.y };
  };

  const handleMouseDown = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setDragging(true);
    const { x, y } = getSvgPoint(e.clientX, e.clientY);
    setInitialMousePosition({ x, y });
    const left = schema.data.left;
    const top = schema.data.top;
    setInitialTablePosition({ left, top });
  };

  const headerHeight = useMemo(() => styles.headerCellHeight * zoom, [zoom]);
  const fieldHeight = useMemo(() => styles.fieldCellHeight * zoom, [zoom]);
  const cellWidth = useMemo(() => styles.defaultCellWidth * zoom, [zoom]);
  const xRectPos = useMemo(() => schema.data.left * zoom, [schema, zoom]);
  const yRectPos = useMemo(() => schema.data.top * zoom, [schema, zoom]);
  const xTextPos = useMemo(
    () => (schema.data.left + styles.cellPaddingX) * zoom,
    [schema, zoom]
  );
  const yTextPos = useMemo(
    () => (schema.data.top + headerCellPaddingY) * zoom,
    [schema, zoom]
  );

  const handleDoubleClick = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setIsModalOpen(true);
  };

  const handleMouseEnter = () => {
    setHoveredIndex(index);
  };
  const handleMouseLeave = () => {
    setHoveredIndex(null);
  };

  const handleTableClick = (e) => {
    e.stopPropagation();
    setContextIndex(index);
  };

  return (
    <>
      <g
        className="table-element"
        key={`table${schema.name}`}
        onMouseEnter={handleMouseEnter}
        onDoubleClick={handleDoubleClick}
        onMouseDown={handleMouseDown}
        onMouseLeave={handleMouseLeave}
        onClick={handleTableClick}
      >
        <TableHeader
          name={schema.name}
          xRectPos={xRectPos}
          yRectPos={yRectPos}
          xTextPos={xTextPos}
          yTextPos={yTextPos}
          height={headerHeight}
          color={schema.data.color}
        />
        {schema.fields.map((field, fieldIdx) => {
          let strokeWidth = 2 * zoom;
          let highlighted = false;
          if (hoveredIndex === index) {
            if (field.fk) {
              highlighted = true;
            }
            const foreignKeyExists = parsedSchemaObjects.some((table) => {
              return table.fields.some((f) => {
                return (
                  f.fk &&
                  f.fk_table === schema.name &&
                  f.fk_field === field.name
                );
              });
            });
            if (foreignKeyExists) {
              highlighted = true;
            }
          }
          if (hoveredIndex !== null) {
            if (
              field.fk &&
              field.fk_table === parsedSchemaObjects[hoveredIndex].name
            ) {
              highlighted = true;
            }
            const hoveredIndexContainsReference = parsedSchemaObjects[
              hoveredIndex
            ]?.fields.some((f) => {
              return (
                f.fk && f.fk_table === schema.name && f.fk_field === field.name
              );
            });
            if (hoveredIndexContainsReference) {
              highlighted = true;
            }
          }
          const cumulativeHeight =
            schema.data.top * zoom + headerHeight + fieldHeight * fieldIdx;
          const fieldTextX = (schema.data.left + styles.cellPaddingX) * zoom;
          const fieldTypeX =
            (schema.data.left + schema.data.width - styles.cellPaddingX) * zoom;
          const fieldY = cumulativeHeight + fieldCellPaddingY;

          let arrowPath = "";
          let startX, startY, endX, endY, cp1x, cp1y, cp2x, cp2y;
          let arrowColor = "red";
          if (field.fk) {
            const targetIndex = parsedSchemaObjects.findIndex(
              (t) => t.name === field.fk_table
            );
            const targetTable = parsedSchemaObjects[targetIndex];

            if (targetTable) {
              if (targetTable.data.color) {
                arrowColor = targetTable.data.color;
              }
              const startXRight = xRectPos + cellWidth;
              const startXLeft = xRectPos;
              startY =
                schema.data.top * zoom +
                headerHeight +
                fieldHeight * fieldIdx +
                fieldHeight * 0.5;
              const endXRight = targetTable.data.left * zoom;
              const endXLeft =
                (targetTable.data.left + targetTable.data.width) * zoom;
              endY =
                targetTable.data.top * zoom +
                headerHeight +
                targetTable.fields.findIndex((f) => f.name === field.fk_field) *
                  fieldHeight +
                fieldHeight * 0.5;
              const distanceRightToLeft = Math.abs(startXRight - endXRight);
              const distanceLeftToRight = Math.abs(startXLeft - endXLeft);
              if (distanceRightToLeft < distanceLeftToRight) {
                startX = startXRight;
                endX = endXRight;
                cp1x = startX + 150;
                cp2x = endX - 150;
              } else {
                startX = startXLeft;
                endX = endXLeft;
                cp1x = startX - 150;
                cp2x = endX + 150;
              }
              cp1y = startY;
              cp2y = endY;

              let deltaX = cp1x - startX;
              let deltaY = cp1y - startY;
              let distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY);
              let angle = Math.atan2(deltaY, deltaX);

              cp1x = startX + Math.cos(angle) * distance * zoom;
              cp1y = startY + Math.sin(angle) * distance * zoom;

              deltaX = cp2x - endX;
              deltaY = cp2y - endY;
              distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY);
              angle = Math.atan2(deltaY, deltaX);

              cp2x = endX + Math.cos(angle) * distance * zoom;
              cp2y = endY + Math.sin(angle) * distance * zoom;

              arrowPath = `M${startX},${startY} C${cp1x},${cp1y} ${cp2x},${cp2y} ${endX},${endY}`;
            }
          }

          return (
            <g key={`fieldGroup${field.name}`}>
              <TableField
                key={`field${field.name}`}
                fieldName={field.name}
                fieldType={field.type}
                xRectPos={xRectPos}
                fieldTextX={fieldTextX}
                fieldTypeX={fieldTypeX}
                pk={field.pk}
                fieldY={fieldY}
                highlighted={highlighted}
                cumulativeHeight={cumulativeHeight}
                height={fieldHeight}
                width={cellWidth}
                color={field.color}
              />
              {arrowPath && (
                <g>
                  <circle
                    cx={startX}
                    cy={startY}
                    r={4 * zoom}
                    fill={arrowColor}
                  />
                  <path
                    d={arrowPath}
                    stroke={arrowColor}
                    fill="transparent"
                    strokeWidth={strokeWidth}
                    markerEnd="url(#arrowhead)"
                  />
                  <defs>
                    <marker
                      id="arrowhead"
                      markerWidth={arrowMarkerSize}
                      markerHeight={arrowMarkerSize}
                      refX={arrowMarkerSize}
                      refY={arrowMarkerSize * 0.54}
                      orient="auto"
                    >
                      <polygon
                        points={`0 0, ${arrowMarkerSize} ${
                          arrowMarkerSize * 0.55
                        }, 0 ${arrowMarkerSize * 1}`}
                        fill={arrowColor}
                      />
                    </marker>
                  </defs>
                </g>
              )}
            </g>
          );
        })}
        {contextIndex === index && (
          <foreignObject
            x={xRectPos}
            y={yRectPos - 60}
            width={styles.defaultCellWidth * zoom}
            height={50}
          >
            <ButtonGroup>
              <Button variant="light" onClick={handleEdit}>
                <AiOutlineEdit />
              </Button>
              <Button variant="light" onClick={handleDelete}>
                <AiOutlineDelete />
              </Button>
              <input
                type="color"
                value={schema.data.color}
                onChange={(e) => handleColorChange(e.target.value)}
                onClick={(e) => e.stopPropagation()}
                style={{ border: "none", width: "40px", height: "40px" }}
              />
            </ButtonGroup>
          </foreignObject>
        )}
      </g>
      <TableModal
        schema={schema}
        isModalOpen={isModalOpen}
        setIsModalOpen={setIsModalOpen}
        tableIndex={index}
      />
    </>
  );
};

export default Table;
